  0% {
    background-position-x: 0%;
    transform: perspective(1000px) rotateX(0deg) scale(1.1);
  }
  25% {
    transform: perspective(1200px) rotateX(-5deg) scale(1.3);
  }
  50% {
    background-position-x: 100%;
    transform: perspective(850px) rotateX(10deg) scale(1.2);
  }
  75% {
    transform: perspective(650px) rotateX(-10deg) scale(1.3);
  }
  100% {
    background-position-x: 0%;
    transform: perspective(1000px) rotateX(0deg) scale(1.1);
  }
}
