html {
  font-size: 62.5%; //1rem = 10px
  overflow-x: hidden;
  background-color: $dark;
  color: #fff;
  @include media-breakpoint-down(lg) {
    font-size: 52.5%;
  }
  @include media-breakpoint-down(md) {
    font-size: 45%;
  }
  @include media-breakpoint-down(sm) {
    // font-size: 30%;
  }
}

body {
  font-size: 1.3rem;
  overflow: hidden;
  background-color: $dark;
  color: #fff;
  // line-height: 1.7;
  letter-spacing: 0.05rem;
  @include media-breakpoint-down(lg) {
    font-size: 1.7rem;

  }
}

h1,
h2.big {
  @include rfs(6.2rem);
  font-weight: 400;
  // line-height: 1;
  // margin-bottom: 2rem;
  // @include media-breakpoint-down(sm) {
  //   font-size: 6rem;
  // }
}

h2 {
  @include rfs(6.2rem);
  font-weight: 700;
  line-height: 0.9;
  @include media-breakpoint-down(lg) {
    @include rfs(5rem);
  }
}

h3 {
  font-size: 4rem;
  font-weight: 700;
  line-height: 0.9;
}

a {
  transition: $transition;
}

.pb3 {
  padding-bottom: 3rem;
}

.text-center {
  text-align: center;
}

.color-dark {
  color: $dark;
}
.color-grey {
  color: $grey;
}
.color-primary {
  color: $primary;
}
.color-secondary {
  color: $secondary;
}
.color-white {
  color: #fff;
}
.color-black {
  color: #000;
}
.bg-primary {
  background-color: $primary;
}

.title {
  font-family: "ethnocentric", sans-serif;
  text-align: center;
  text-transform: uppercase;
  color: $dark;
  -webkit-text-stroke: 1px #fff;
  letter-spacing: var(--title-spacing);
  // @include media-breakpoint-up(lg){
  // animation: 13s ease spacing infinite;
  // }
}

.subtitle {
  font-size: 3rem;
  letter-spacing: 0.01rem;
  // color: $grey;
}

.btn {
  font-size: 1.3rem;
  font-weight: 500;
  // letter-spacing: 0.01rem;
  line-height: 1;
  border-radius: $border-radius-b;
  padding: 1.3rem 3rem;
  text-transform: uppercase;
  &:hover,
  &:focus,
  &:active {
    box-shadow: none;
    outline: none;
  }
  &-primary {
    color: #fff;
    background-color: $primary;
    &:hover {
      color: #fff;
    }
  }
  &-secondary {
    color: #fff;
    background-color: $secondary;
    &:hover {
      color: #fff;
    }
  }
  &-tertiary {
    color: #fff;
    background-color: $tertiary;
    border: 2px solid transparent;
    &:hover {
      color: #fff;
      background-color: transparent;
      border-color: $tertiary;
    }
  }
  &-white {
    color: $dark;
    background-color: #fff;
    &:hover {
      color: #fff;
      background-color: $primary;
    }
  }
  &-gradient {
    color: #fff;
    background: $gradient;
    background-position: 50% 50%;
    background-size: 200% 100%;
    animation: btn-grad-b 1s ease;

    &:hover {
      color: #fff;
      animation: btn-grad 1s ease;

      // background-size: 200% 100%;
      // background-position: 200% 50%;
    }
  }
}

@keyframes btn-grad {
  0% {
    background-position: 50% 50%;
  }
  100% {
    background-position: -200% 50%;
  }
}
@keyframes btn-grad-b {
  0% {
    background-position: -200% 50%;
  }
  100% {
    background-position: 50% 50%;
  }
}

.btn-outline-secondary {
  border-width: 2px;
  color: $secondary;
  border-color: $secondary;
  &:hover {
    color: #fff;
    background-color: rgba($secondary, 0.85);
  }
  &:focus,
  &:active {
    outline: none;
    box-shadow: none;
    background-color: $secondary;
    color: #fff;
  }
}

.container-wide {
  @include make-container(5rem);
  max-width: 1680px;
}

.title-line {
  position: relative;
  > * {
    position: relative;
  }
  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    height: 17.6rem;
    width: 450rem;
    background-image: url("../img/title-line.svg");
    background-repeat: no-repeat;
    background-size: cover;
    // background-position: -300%;
  }
  &--about {
    &::before {
      right: -230rem;
    }
  }

  &--cs {
    &::before {
      right: -100rem;
    }
  }
  &--influ {
    &::before {
      right: -180rem;
    }
  }
  &--projects {
    &::before {
      right: -90rem;
    }
  }

  &--footer {
    &::before {
      right: -30rem;
    }
  }
}
